.container{
    margin: 0 20px;
    display: grid;
    grid-template-columns: auto auto auto;
    background-attachment: fixed;
}
.v{
    background: rgba(0, 0, 0, 0.287);
    border-radius: 10px;
    box-shadow: 10px 10px 10px rgba(0, 229, 255, 0.496);
    margin: 15px;
    text-align: center;
    width: 452px;
    transition: all 0.5s ease-out;

}
.txt{
    color: white;
}
.v img{
    border: none;
    border-radius: 10px;
    box-shadow: 10px 10px 10px black;
    height: 250px;
    width: 450px;
}
.v:hover{
    transform: scale3d(1.05,1.05,1.05);
}

.v>div {
    border-radius: 10px;
}