*{
    margin: 0;
    padding: 0;
}
.contactpg {
    background: var(--second-bg-color);
    height: 85vh;
    font-weight: 600;
    font-family: sans-serif;
}
.contact-box {
    background: var(--second-bg-color);
    border: 1px solid rgba(128, 128, 128, 0.797);
    border-radius: 10px;
    height: 70vh;
    width: 46vw;
    padding: 20px 30px;
}

h1{
    text-align: center;
}

input,textarea{
    display: block;
    height: 45px;
    width: 100%;
    border-radius: 10px;
    outline: none;
    margin: 10px;
    border: 0.5px solid rgb(221, 213, 213);
    background-color: rgba(239, 237, 237, 0.876);
    font-size: 0.8rem;
}

input[placeholder],textarea{
    padding: 10px;
}
input[type="submit"]{
    background: black;
    color: white;
}

textarea{
    height: 150px;
}
label{
    display: flex;
    align-items: center;
}

.flex{
    display: flex;
}